.consult {
	background-image: url("../img/consult-fon.svg");
	background-position: top -40px left -10px;
	padding: 90px calc((100% - 1170px)/2) 127px;
	display: flex;
	justify-content: space-between;
	align-items: center;
		
	@media (max-width: 1023px) {
	background-image: none;
	padding: 5% 11% 13%;
	}
	
	@media (max-width: 767px) {
	padding: 53px 4% 34px;
	}
	
	.consult__item {
	
	&:first-child {
	flex: 1 1 570px;
	height: 574px;
	background: url("../img/plane.webp") no-repeat;
	background-position: top 80px right 90px;
	background-size: 511px 288px;
	
	
	@media (max-width: 1023px) {
	display: none;
	}
	}
	
	&:last-child {
	flex: 0 0 570px;
	
	@media (max-width: 1023px) {
	flex: 1 1 100%;
	}
	}
	}
}

.consult__form {
	
	h3 {
	margin-top: 0;
	margin-bottom: 25px;
	}
	
	p {
	margin-bottom: 52px;
	
	@media (max-width: 767px) {
	margin-bottom: 30px;
	}
	}
	
	.consult__input {
	display: flex;
	
	@media (max-width: 767px) {
	display: block;
	}
	
	input[type="text"]	{
	margin-right: 30px;
	}
	}
	
	@media (max-width: 1023px) {
	width: 100%;
	height: auto;
	margin: 0 auto;
	padding: 0;
	}
	
	@media (max-width: 767px) {
	width: 100%;
	height: auto;
	margin: 0 auto;
	padding: 0;
	}
	
	label {
	width: 40%;
	
	@media (max-width: 767px) {
	width: 82%;
	}
	
	&:before {
/*как задать ширину*/
	margin-right: 16px;
	}
	}
}


.consult__button {
	width: 47%;
	margin-right: 39px;
	padding: 14px 50px;
	border-radius: 5px;
	display: inline-block;
	
	@media (max-width: 767px) {
	width: 100%;
	margin: 0 0 30px 0;
	}
	
	&:hover {
	cursor: pointer;
	}
}

